.custom-btn {
    width: 130px;
    height: 40px;
    color: #fff;
    border-radius: 5px;
    padding: 10px 25px;
    font-family: 'Lato', sans-serif;
    font-weight: 500;
    background: transparent;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative;
    display: inline-block;
    box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, .5),
        7px 7px 20px 0px rgba(0, 0, 0, .1),
        4px 4px 5px 0px rgba(0, 0, 0, .1);
    outline: none;
    margin-bottom: 10px;
}

.add-new-task {
    width: 130px;
    height: 40px;
    line-height: 42px;
    padding: 0;
    border: none;
    background: #1C0C5B;
    background: linear-gradient((0deg, #1C0C5B) 0%, #3D2C8D 100%);
}

.btn {
    justify-content: center;
    display: flex;
}

.add-new-task:hover {
    color: #170055;
    background: transparent;
    box-shadow: none;
}

.add-new-task:before,
.add-new-task:after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    height: 2px;
    width: 0;
    background: #170055;
    box-shadow:
        -1px -1px 5px 0px #fff,
        7px 7px 20px 0px #0003,
        4px 4px 5px 0px #0002;
    transition: 400ms ease all;
}

.add-new-task:after {
    right: inherit;
    top: inherit;
    left: 0;
    bottom: 0;
}

.add-new-task:hover:before,
.add-new-task:hover:after {
    width: 100%;
    transition: 800ms ease all;
}


:focus {
    outline: none;
}

.input {
    float: left;
    margin: 20px 3%;
