/* necessary to give position: relative to parent. */
input[type="text"] {
    font: 15px/24px "Lato", Arial, sans-serif;
    color: #333;
    width: 100%;
    box-sizing: border-box;
    letter-spacing: 1px;
}

.effect {
    border: 1px solid rgb(98, 91, 161);
    padding: 7px 14px 9px;
    transition: 0.4s;
    border-radius: 5px;
}



.effect~.focus-border:before,
.effect~.focus-border:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 2px;
    background-color: #170055;
    transition: 0.3s;
}

.effect~.focus-border:after {
    top: auto;
    bottom: 0;
    left: auto;
    right: 0;
}

.effect~.focus-border i:before,
.effect~.focus-border i:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 2px;
    height: 0;
    background-color: #170055;
    transition: 0.4s;
}

.effect~.focus-border i:after {
    left: auto;
    right: 0;
    top: auto;
    bottom: 0;
}

.effect:focus~.focus-border:before,
.effect:focus~.focus-border:after {
    width: 100%;
    transition: 0.3s;
}

.effect:focus~.focus-border i:before,
.effect:focus~.focus-border i:after {
    height: 100%;
    transition: 0.4s;
}


